     'Blaming the victims': 'Blaming the victims',
     para1: 'In May 2013, the traffic court of Moabit fined a truck driver who had reversed over a 71-year-old to pay 1,200 euros. In justifying their decision, the court said that the elderly should have seen the truck coming towards him. As if the old man had willingly jumped under the lorry.',
     para2: 'Blaming victims of road homicides instead of perpetrators is common practice in the Berlin police and judiciary. In their <a href="https://www.berlin.de/polizei/_assets/aufgaben/anlagen-verkehrssicherheit/radfahrer2017.pdf" target="_blank">yearly report</a> on crashes involving cyclists, for instance, the police excuses cars and trucks systematically (page 5). Cars crash into cyclists when they turn right? This is because of their blind spots. Cars refuse priority to cyclists? This is because of the cyclists\' "thin silouhette". Cars crash into cyclists when they drive out of a garage? The sidewalks are hard to see. On the other hand, the police report excuses none of the situations where cyclists do cause accidents. If cyclists run into cars at traffic lights, it is not because of inadequate or inexistant cycling lanes, writes the police, but because cyclists do not keep the appropriate safety distance.',
