     para4: 'Es ist schon passiert, dass Berliner Staatsanwälten wegen Mord oder Mordversuch gegen Autofahrer gegangen sind. In einem Fall wurden Raser, die auf dem Kudamm einen anderen Autofahrer getötet hatten, <a href="https://www.bz-berlin.de/tatort/menschen-vor-gericht/verteidiger-der-kudamm-raser-plaedieren-auf-fahrlaessigkeit" target="_blank">wegen Mord verfolgt</a>. In einem anderen Fall wurde einen Mann, der eine Polizeikontrolle entgehen wollte, in einer kleinen Straße rastete und ein Kind und seine Mutter fast totfuhr, <a href="https://www.rbb24.de/panorama/beitrag/2018/09/autoraser-berlin-urteil-versuchter-mord.html" target="_blank">mit Mordversuch angeklagt</a> (der Richter gab ihn 13 Jahre haft).',
     para5: 'In Deutschland ist Autofahrer als Mörder keine Selbstverständlichkeit. Der erste Fall, die Raser am Kudamm, wurde vor dem Bundesgerichtshof <a href="http://juris.bundesgerichtshof.de/cgi-bin/rechtsprechung/document.py?Gericht=bgh&Art=en&Datum=Aktuell&nr=81300&linked=pm" target="_blank">aufgehoben</a>. Dass man 160 Kilometer pro Stunde Mitte in der Stadt fährt heißt nicht, dass man töten will, sagten die Karlsruher Richter.',
